.accountModal {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(7, 0, 29, 0.7);
  backdrop-filter: blur(2px);
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  &-inner {
    background-origin: border-box;
    background-clip: content-box, border-box;
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    &-inner {
      width: 80%;
      max-width: 720px;
      padding: 30px 20px;
      background-color: #fff;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
    }

    &-box {
      &-header {
        padding: 10px 0;
        border-bottom: 1px solid #a8a9a3;
        font-size: 16px;
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        align-items: center;
        color: #565656;
        &-copy {
          padding: 5px 10px;
          background: rgb(0, 0, 0, 0.3);
          color: #fff;
          border-radius: 40px;
          cursor: pointer;
        }
      }
      &-body {
        padding: 15px 0;
        font-size: 14px;
        color: #565656;
        line-height: 1.5;
      }
    }

    &-button {
      background: rgb(0, 0, 0, 0.5);
      color: #fff;
      line-height: 40px;
      text-align: center;
      width: 100%;
      border-radius: 5px;
      cursor: pointer;
    }
  }
}
